import { Component } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { NavController, ModalController } from 'ionic-angular';
import { FilterPipe } from './../../filter.pipe';
import { VehicleBrandsPage } from './../vehicle-brands/vehicle-brands'
import { VehiclePartsSelectPage } from './../vehicle-parts-select/vehicle-parts-select'
import { DataServiceProvider } from '../../providers/data-service/data-service';
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  myForm: FormGroup;

  constructor(public navCtrl: NavController, public dataService: DataServiceProvider, private fb: FormBuilder, public modalCtrl: ModalController) {


    this.myForm = fb.group({
      vehicleType: ['', Validators.required],
      vehicleBrand: this.fb.group({
        bname: ['', Validators.required],
        code: ['', Validators.required]
      }),
      condition: ['', Validators.required],
      vehicleCat: this.fb.group({
        cname: ['', Validators.required],
        code: ['', Validators.required]
      }),
      part: this.fb.group({
        pname: ['', Validators.required],
        code: ['', Validators.required]
      }),
      district: ['', Validators.required]
    });
    this.myForm.get('vehicleType').valueChanges.subscribe(val => {
      this.myForm.get('vehicleBrand.bname').setValue('');
      this.myForm.get('vehicleBrand.code').setValue('');
    });
  }

  setType(vehicle: String) {
    this.myForm.controls['vehicleType'].setValue(vehicle);
  }
  setCond(cond) {
    this.myForm.controls['condition'].setValue(cond)
  }

  selVehicleType(vehicleType: string) {
    let vehicleModal = this.modalCtrl.create('VehicleBrandsPage', { vType: vehicleType });
    vehicleModal.onDidDismiss(data => {
      if (data) {
        this.myForm['controls'].vehicleBrand['controls'].bname.setValue(data.name);
        this.myForm['controls'].vehicleBrand['controls'].code.setValue(data.code);
      }
    });
    vehicleModal.present();
  }

  selPartType() {
    let partModal = this.modalCtrl.create('VehiclePartsSelectPage');
    partModal.onDidDismiss(data => {
      if (data) {
        this.myForm['controls'].part['controls'].pname.setValue(data.name);
        this.myForm['controls'].part['controls'].code.setValue(data.code);
      }
    });
    partModal.present();
  }

  selVehicleCat() {
    let partModal = this.modalCtrl.create('VehicleTypePage');
    partModal.onDidDismiss(data => {
      if (data) {
        this.myForm['controls'].vehicleCat['controls'].cname.setValue(data.name);
        this.myForm['controls'].vehicleCat['controls'].code.setValue(data.code);
      }
    });
    partModal.present();
  }

  viewItems(val) {
    this.navCtrl.push('ViewItemsPage', { data: this.myForm.value });
  }

  addDealer() {
    this.navCtrl.push('AddBusinessPage');
  }
  addPartstoDealer() {
    this.navCtrl.push('AddPartsToBusinessPage');
  }
  selDistrict() {
    let districtModal = this.modalCtrl.create('SelectDistrictPage');
    districtModal.onDidDismiss(data => {
      if (data) {
        this.myForm.controls['district'].setValue(data);
      }
    });
    districtModal.present();
  }
  //get form() {return this.myForm.controls. };
  getParts() {
    let code = this.myForm.controls['vehicleType'].value + this.myForm['controls'].vehicleBrand['controls'].code.value + this.myForm.controls['condition'].value + this.myForm['controls'].vehicleCat['controls'].code.value + this.myForm['controls'].part['controls'].code.value;

    this.dataService.getParts(this.myForm.controls['district'].value, code).subscribe(data => {
      this.navCtrl.push('ViewItemsPage', { data: data });
    });
  }
}





